* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: rgb(152, 224, 224);
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
input {
  border: none;
  outline: none;
  background-color: transparent;
  width: 100%;
  padding: 20px;
  color: white;
  font-size: 20px;
  text-align: right;
  font-weight: bold;
  word-break:normal;

}
#calculator {
  border: 8px solid white;
  background-color: black;
  color: white;
  box-sizing: border-box;
  width: 300px;
  
  border-radius: 5px;

}
#button {
  justify-content: center;
  display: grid;
  width: 100%;
  grid-template-rows: repeat(5, 60px);
  grid-template-columns: repeat(4, 60px);
  grid-gap: 5px;
  margin: 10px 0px;
}
button {
  border-radius: 6px;
  color: white;
  background-color: rgb(22, 22, 22);
  font-size: 20px;
  font-weight: bolder;
  border: none;
  outline: none;
}
.ce{
  grid-column: 1/3;
}
.equal{
  grid-column: 3/5;
}
.cyan{
  background-color: rgb(14, 199, 199);
  color: black;
}